.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid rgb(231, 231, 231);
  width: 100%;
}
.nav__container {
  width: 80%;
  transition: all 0.3s ease-in-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}

.nav__group {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav__group a {
  color: black;
  margin: 0 20px;
}

.nav__group a div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__group a div p {
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 2px;
  margin: 0 5px;
}

.nav__shrink {
  height: 30px;
}



@media all and (max-width: 880px) {
  .container p{
    display: none;
  }

  .nav__container{
    width: 95%;
  }

  .logo{
    font-size: 20px;
  }

  .container a div svg{
    font-size: 20px;
  }
}


@media all and (max-width:590px) {
  .logo{
    font-size: 12px;
  }
}