.footer {
  width: 100%;
  min-height: 90vh;
  background-color: #232323;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.container div {
  width: 15rem;
  min-height: 12rem;
  text-align: center;
  align-items: center;
}
.container div h5 {
  color: white;
  font-size: large;
}

.container div a {
  display: block;
  margin: 10px 0;
  color: #bfbfbf;
}

.lastline {
  color: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
}
.container div a:hover {
  color: white;
}

.contact_form div {
  align-items: center;
  text-align: center;
}

.form__input {
  margin: 15px 5px;
  padding: 10px 10px;
  width: 250px;
  outline: none;
  border: 1px solid #bbb;
  border-radius: 3px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -ms-transition: 0.2s ease all;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
}

.form__input__email {
  margin: 5px 5px;
  padding: 10px 10px;
  width: 400px;
  outline: none;
  border: 1px solid #bbb;
  border-radius: 3px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -ms-transition: 0.2s ease all;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
}

.form__button {
  margin: 5px 5px;
  padding: 7px 25px;
  border: 1px solid #bbb;
  color: white;
  background-color: rgb(22, 133, 22);
  border-radius: 3px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -ms-transition: 0.2s ease all;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
}


.form__and__logo
{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}