.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid rgb(231, 231, 231);
  width: 100%;
}
.nav__container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}

.nav__group {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav__group a {
  color: black;
  margin: 0 20px;
}

.nav__group a div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__group a div p {
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 2px;
  margin: 0 5px;
}

.nav__shrink {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  transition: all 0.3s ease-in-out;
}


.ham__button
{
  display: none;
  cursor: pointer;
  position: fixed;
  align-items: center;
  text-align: center;
  top: 100px;
  left: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.ham__button div{
  background-color: rgb(105, 163, 255);
  width: 30px;
  height: 3px;
  margin: 6px 0;
}

@media all and (max-width: 880px) {
  .container {
    display: none;
  }

  .ham__button{
    display: block;
  }
}
